
import * as apiMethods from './apiMethods';

class api {
    
    static signUpToSite(userData) {
        return apiMethods.apiPost({
                                    postData: userData,
                                    url: '/signUp',
                                    logError: "error in logIngApi - signUpToSite() :"
                                });
    }
    static loginToSite(userData) {
        return apiMethods.apiPost({
                                    postData: userData,
                                    url: '/login',
                                    logError: "error in logIngApi - loginToSite() :"
                                });
    }
}

export default api;