import _ from 'lodash';

export function onChange({key: key, index: index, dataKey: dataKey, item: item, self: self}) {
    let object = self.state;
    let data = _.cloneDeep(object[self.dictionary.data]); //example: [...this.state.data]
    data[index][dataKey] = item;      //example: data[index][this.dictionary.currencyUsed] = item;
    object[self.dictionary.data] = data;
    object[key] = item;
    self.updateChanges(data[index]);
    self.setState(object);
}

export function onChangeDropdownList(item, field, self) {
    const object = self.state;
    object[field] = item;
    self.setState(object);
}
export function onChangeTextInput(item, field, self) {
    const object = self.state;
    object[item] = field;
    self.setState(object);
}