
import localServicesDictionary from './localStorageDictionary';

export function setAuthDataToLocalStorage(userData){
  localStorage.setItem(localServicesDictionary.accessToken,  userData.accessToken);
  localStorage.setItem(localServicesDictionary.refreshToken,  userData.refreshToken);
  localStorage.setItem(localServicesDictionary.userId, userData.userId);
  }


export function emptyLocalStorage(){
  localStorage.removeItem(localServicesDictionary.refreshToken);
  localStorage.removeItem(localServicesDictionary.userId);
  localStorage.removeItem(localServicesDictionary.accessToken);
}
